import { _decorator, Animation, Button, Component, Node } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('ResultScreen')
export class ResultScreen extends Component {
    
    @property({type:Node})
    private win = null;
    
    @property({type:Node})
    private fail = null;

    @property({type:Animation})
    private animation = null;

    @property({type:Button})
    private touchBlocker = null;

    show(isWin: boolean) {

        this.win.active = isWin;
        this.fail.active = !isWin;

        this.touchBlocker.enabled = true;
        this.animation.play();
    }
}
